.App {
    display: flex;
    font-family: Arial,Helvetica,sans-serif;
    min-height: 100%
}

.clima-container {
    background-color: #7fffd4;
    flex: 1 1;
    flex-wrap: wrap;
    font-family: Arial,Helvetica,sans-serif;
    font-size: medium;
    justify-content: space-between;
    padding: .5%
}

.clima-container,.transporte-container {
    border: .25vh solid #ccc;
    box-sizing: border-box;
    min-height: 100%;
    min-width: 50%
}

.transporte-container {
    background-color: bisque;
    flex: 1 1;
    padding: .5%
}

.ciudad,.fecha-hora {
    background-color: #fff8dc;
    border: .25vh solid #ddd;
    border-radius: 5px;
    flex: 1 1;
    gap: 1%;
    margin: .25%;
    min-width: 300px;
    padding: .25%;
    text-align: center
}

.info-general {
    grid-gap: .25%;
    display: grid;
    gap: .25%;
    grid-template-columns: repeat(2,1fr)
}

.estado,.temperatura {
    margin: auto
}

.estado-clima,.temp-actual {
    background-color: #3c99d6;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 0,25%;
    text-align: center
}

.icon-temp {
    height: auto;
    margin-right: 5px;
    width: 20%
}

.icon-clima {
    width: 20%
}

.icon {
    width: 15%
}

.icon-amanecer,.icon-atardecer {
    width: 10%
}

.icon-max,.icon-min {
    width: 12%
}

.info-detallada {
    grid-gap: .1%;
    align-items: center;
    display: grid;
    gap: .1%;
    grid-template-columns: repeat(2,1fr);
    grid-template-rows: repeat(2,1fr)
}

.aire,.humedad,.viento,.visibilidad {
    background-color: #f4f4f4;
    border: 1px solid #ccc;
    border-radius: 5px;
    box-shadow: 0 2px 4px rgba(0,0,0,.1);
    margin-bottom: .25%;
    min-width: 50%;
    padding: .25%;
    text-align: center;
    transition: background-color .3s ease
}

.salida-puesta,.temp-min-max {
    grid-gap: .25%;
    align-items: center;
    display: grid;
    gap: .25%;
    grid-template-columns: repeat(2,1fr)
}

.sol-info {
    align-items: center;
    display: grid;
    grid-template-columns: repeat (2,1fr)
}

.puesta-sol,.salida-sol {
    box-shadow: 0 2px 4px rgba(0,0,0,.1);
    transition: background-color .3s ease
}

.puesta-sol,.salida-sol,.temp-max,.temp-min {
    background-color: #f4f4f4;
    border: 1px solid #ccc;
    border-radius: 5px;
    margin-bottom: .25%;
    min-width: 50%;
    padding: .25%;
    text-align:center
}

.temperatura-por-hora {
    display: flexbox;
    flex: 1 1;
    gap: .25%;
    min-width: auto
}

.lista-temperaturas {
    background-color: #766099;
    border-radius: 2%;
    display: flex;
    gap: 2.7%;
    margin-top: 3%;
    min-width: 60%;
    overflow-x: auto;
    padding: .5%;
    white-space: nowrap
}

.temperatura-hora {
    align-self: self-end;
    background-color: #fff8dc;
    border: 2% solid #b16430;
    border-radius: 2%;
    display: flex;
    flex-direction: column;
    font-size: small;
    padding: 1%;
    text-align:center;
}

.leaflet-container {
    width: 100wh; 
    height: 100vh;
  }